import React from 'react';
import { withRouter } from 'react-router-dom';
import axios from 'axios';

class Posters extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            movies: []
        }
    }

    componentWillMount = () => {
        console.log('bitch');
        this.getMovies();
    }

    getMovies = () => {
        axios.get("http://localhost:65332/api/poster/getall")
            .then(resp => {
                // let arr = resp.items;          
                // console.log(resp);
                // this.setState({
                //     arr
                // })
               

                this.setState({movies: resp.items})
                
            })
    }

    render() {

        return (
            <div>

                {movies.map((movie) => 
                   <div> key={movie.id}
                   </div>
                )}        
        </div>
        );
}
}
export default Posters;


